import React from 'react';
import { useSteamMostRecentlyPlayed } from './hooks/steamHooks';
import Typography from '@material-ui/core/Typography'
import { styled } from '@material-ui/core/styles';

const MyTypography = styled(Typography)({
  color: '#b8b6b4'
});
//http://media.steampowered.com/steamcommunity/public/images/apps/214950/08ae0b3454bfe8e7ae9038331b27629667119a24.jpg
const SteamMostRecentGame = (props) => {
    const mostRecentGameInfo = useSteamMostRecentlyPlayed(props.steamDetails.id);

    return <div>
        <MyTypography variant='body1' align='center' gutterBottom>        
            Last Played: {mostRecentGameInfo.name}  
        </MyTypography>
        <img src={`http://media.steampowered.com/steamcommunity/public/images/apps/${mostRecentGameInfo.appid}/${mostRecentGameInfo['img_logo_url']}.jpg`} />
    </div>
}

export default SteamMostRecentGame;